import React, {useEffect, useState} from 'react';
import Navbar from "../component/Navbar";
import {Card} from "react-bootstrap";

const MainPage = () => {
  return (
    <div className="default_main">
      <Navbar />
            <Card className="main_card" body>Celem pracy jest implementacja aplikacji internetowej korzystajcej z autoregresyjnego modelu jzyka GPT-3, ktra na danie generuje tre uzasadnie wyrokw sdowych i posiada interfejs do subiektywnej oceny jakoci tych uzasadnie. Aplikacja ma na celu zweryfikowanie, czy dla zadanego kazusu, uycie GPT-3 moe wspomc prac prawnika przez prezentacj moliwej linii argumentacyjnej generowanej na podstawie bazy orzecze pochodzcej z Systemu Analizy Orzecze Sdowych.</Card>
    </div>
  );
};

export default MainPage;
